
.u-cell {
  width: 100%;
  height: 100%;
  position: relative;
  display: table;
}
.u-cell .u-cell-wrap {
  width: 100%;
  max-width: 0px;
  padding: 0;
  vertical-align: middle;
  display: table-cell;
}
.u-cell .u-cell-text {
  padding: 0 5px;
  word-wrap: break-word;
}
.u-cell .u-cell-checkbox {
  cursor: pointer;
}
.u-cell .u-cell-comment {
  border: 4px solid;
  border-color: transparent transparent transparent transparent;
  width: 0px;
  height: 0px;
  position: absolute;
  top: 0px;
  right: 0px;
  z-index: 900;
}
.u-cell .u-cell-comment.error {
  border-color: #b94a48 #b94a48 transparent transparent;
}
.u-cell .u-cell-comment.warning {
  border-color: #f89406 #f89406 transparent transparent;
}
.u-cell .u-cell-comment.success {
  border-color: #468847 #468847 transparent transparent;
}
.u-cell .u-cell-comment.info {
  border-color: #3a87ad #3a87ad transparent transparent;
}
.u-cell .u-cell-comment.changed {
  border-color: #f00 #f00 transparent transparent;
}
.u-cell .u-cell-tree-field-icon {
  position: absolute;
  color: #777;
  cursor: pointer;
  text-align: right;
}



