
.u-layout-row {
  min-height: 34px;
  padding-bottom: 4px;
  margin-bottom: 12px;
}
.u-layout-row:after {
  content: "";
  /*      display: block; */
  height: 0;
  clear: both;
  overflow: hidden;
  visibility: hidden;
}
.u-layout-cell-label {
  display: table-cell;
  text-align: right;
  margin-right: 10px;
  vertical-align: top;
  height: 28px;
  line-height: 28px;
  font-weight: bold;
  margin-bottom: 0;
  padding-right: 10px;
  font-size: 14px;
}
.u-layout-cell-label:after {
  content: ":";
  display: inline-block;
  margin-right: 4px;
  line-height: 1;
  font-family: SimSun;
  font-size: 12px;
  color: black;
}
.u-layout-cell-field {
  display: table-cell;
  width: 2000px;
  vertical-align: middle;
  /* border-bottom: 1px solid #ccc; */
}
.u-layout-cell-field .u-generic-input-text {
  font-size: 14px;
  color: #333;
}
.u-layout-cell-field .u-layout-cell-help {
  display: block;
  color: #9ea7b4;
  font-size: 12px;
  margin-top: 5px;
  margin-left: 2px;
}
.u-layout-compact .u-layout-cell-label {
  display: inline-block;
  margin-right: 0px;
  padding-right: 0px;
}
.u-layout-compact .u-layout-cell-field {
  display: inline-block;
  margin-right: 10px;
}
.u-layout-required .u-layout-cell-label:before {
  content: "*";
  display: inline-block;
  margin-right: 4px;
  line-height: 1;
  font-family: SimSun;
  font-size: 12px;
  color: #ed3f14;
}
.u-layout-error .u-layout-cell-error {
  display: block;
  color: #ed3f14;
  font-size: 12px;
  margin-top: 5px;
  margin-left: 2px;
}
.u-layout-error .ivu-input,
.u-layout-error .ivu-select-selection,
.u-layout-error .vue-treeselect__control {
  border: 1px solid #ed3f14;
}



