
.u-sort {
  position: absolute;
  width: 7px;
  height: 10px;
  color: #999;
  cursor: pointer;
  top: 50%;
  right: 8px;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
}
.u-sort .u-sort-up {
  position: absolute;
  top: -5px;
}
.u-sort .u-sort-down {
  position: absolute;
  bottom: -5px;
}
.u-sort.up .u-sort-up {
  color: black;
}
.u-sort.down .u-sort-down {
  color: black;
}



