
.ivu-btn-group {
  margin-right: 3px;
}
label {
  margin-bottom: 0px;
}
.u-grid-wrapper .u-grid-tools {
  margin-bottom: 5px;
  height: 25px;
}
.u-grid-wrapper .u-grid-tools:after {
  content: "";
  display: block;
  height: 0;
  clear: both;
  overflow: hidden;
  visibility: hidden;
}
.u-grid-wrapper .u-grid-tools .u-grid-tools-left {
  float: left!important;
}
.u-grid-wrapper .u-grid-tools .u-grid-tools-right {
  float: right!important;
}
.u-grid-wrapper .u-grid {
  position: relative;
  font-size: 14px;
}
.u-grid-wrapper .u-grid > .column-dragger-guide {
  height: 100%;
  top: 0px;
  left: 0px;
  border-left: 2px dashed green;
  position: absolute;
  z-index: 1000;
}
.u-grid-wrapper .u-grid > .loading {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: auto;
  height: 34px;
  text-align: center;
  color: black;
  line-height: 34px;
  border: 1px solid gray;
  min-width: 100px;
  background-color: antiquewhite;
  z-index: 9999;
}
.u-grid-wrapper .u-grid .u-table-left,
.u-grid-wrapper .u-grid .u-table-right {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  overflow: hidden;
  border-right: none;
  border-bottom: none;
}
.u-grid-wrapper .u-grid .u-table-left.dark,
.u-grid-wrapper .u-grid .u-table-right.dark {
  -webkit-box-shadow: 1px 0px 2px #999;
          box-shadow: 1px 0px 2px #999;
}
.u-grid-wrapper .u-grid .u-table-left .u-table-body-scroll,
.u-grid-wrapper .u-grid .u-table-right .u-table-body-scroll {
  overflow: hidden;
}
.u-grid-wrapper .u-grid .u-table-right {
  top: 0;
  right: 0;
  left: auto;
  border-left: 1px solid #ddd;
  border-right: 1px solid #ddd;
}
.u-grid-wrapper .u-grid .u-table-right.dark {
  -webkit-box-shadow: -1px 0px 2px #999;
          box-shadow: -1px 0px 2px #999;
}
.u-grid-wrapper.theme-simple .u-table {
  border: none;
}
.u-grid-wrapper.theme-simple .u-table .u-table-header-wrapper .u-table-header-scroll table.u-table-header th {
  border: none;
  border-bottom: 1px solid #eee;
  background-color: #eee;
}
.u-grid-wrapper.theme-simple .u-table .u-table-body-scroll table tr td {
  border: none;
  border-bottom: 1px solid #eee;
}



