
.nowrap {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
  -ms-text-overflow: ellipsis;
}
.u-table {
  position: relative;
  overflow: hidden;
  background: #fff;
  border: 1px solid #ccc;
  text-align: left;
}
.u-table .u-table-header-wrapper {
  overflow: hidden;
  position: relative;
  background-color: #eee;
}
.u-table .u-table-header-wrapper .u-table-header-scroll {
  z-index: 1;
  height: 100%;
  overflow: hidden;
  position: relative;
  background-color: #eee;
}
.u-table .u-table-header-wrapper .u-table-header-scroll table {
  border-collapse: separate;
  border-spacing: 0;
  text-align: left;
  table-layout: fixed;
  margin: 0;
  display: table;
}
.u-table .u-table-header-wrapper .u-table-header-scroll table.u-table-header {
  position: relative;
  overflow: hidden;
  z-index: 1;
}
.u-table .u-table-header-wrapper .u-table-header-scroll table.u-table-header th {
  position: relative;
  padding: 0;
  border: none;
  border-right: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
.u-table .u-table-body-scroll {
  overflow: auto;
  position: relative;
}
.u-table .u-table-body-scroll table {
  border-collapse: separate;
  border-spacing: 0;
  text-align: left;
  table-layout: fixed;
  margin: 0;
  display: table;
}
.u-table .u-table-body-scroll table tr.selected {
  background-color: #ffefd5;
}
.u-table .u-table-body-scroll table tr.hover {
  background-color: #e1eff8;
}
.u-table .u-table-body-scroll table tr td {
  position: relative;
  padding: 0;
  border: none;
  border-right: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
.u-table .u-table-body-scroll .u-table-no-data {
  margin: 0 auto;
  text-align: center;
}
.u-table.auto-height .u-table-body-scroll tr:last-child td {
  border-bottom: none;
}
.u-table .u-table-header-cell-resizer {
  position: absolute;
  width: 4px;
  cursor: col-resize;
  top: 0px;
  right: 0px;
  height: 100%;
}



