
.pagination {
  list-style: none;
  display: inline-block;
  padding-left: 0;
  margin: 8px 0;
  width: 100%;
  font-size: 14px;
}
.pagination:after {
  content: "";
  display: block;
  height: 0;
  clear: both;
  overflow: hidden;
  visibility: hidden;
}
.pagination ul {
  display: inline-block;
}
.pagination .page-item {
  display: inline-block;
  vertical-align: middle;
  min-width: 24px;
  height: 24px;
  line-height: 24px;
  margin-right: 4px;
  text-align: center;
  list-style: none;
  background-color: #fff;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-family: Arial;
  border: 0;
  border-radius: 2px;
  -webkit-transition: border 0.2s ease-in-out, color 0.2s ease-in-out;
  transition: border 0.2s ease-in-out, color 0.2s ease-in-out;
  margin-top: 0;
}
.pagination .page-item > a {
  margin: 0;
  text-decoration: none;
  color: #495060;
  padding: 3px 6px;
  border-radius: 0px;
  border: 1px solid #eee;
}
.pagination .page-item:hover {
  border-color: #2d8cf0;
  background-color: whitesmoke;
}
.pagination .page-item:hover a {
  color: #2d8cf0;
}
.pagination .page-item.page-input {
  border: 1px solid #eee;
  border-radius: 0px;
  padding: 0 7px;
}
.pagination .page-total {
  height: 24px;
  line-height: 24px;
  margin: 0px 8px;
  float: right!important;
}
.pagination .page-prev,
.pagination .page-next,
.pagination .page-first,
.pagination .page-last {
  min-width: 20px;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
}
.pagination .page-disabled a {
  cursor: not-allowed;
}
.pagination .page-active {
  background-color: #2d8cf0;
  border-color: #2d8cf0;
}
.pagination .page-active a {
  color: #fff;
}
.pagination .page-active:hover a {
  color: #fff;
}
.pagination .page-options {
  display: inline-block;
  vertical-align: middle;
  margin-right: 4px;
  background-color: #fff;
}
.pagination .page-options .page-options-elevator,
.pagination .page-options .page-options-sizer {
  display: inline-block;
}
.pagination .page-options .select-dropdown {
  width: 78px;
  -webkit-transform-origin: center top 0px;
          transform-origin: center top 0px;
  z-index: 9999;
}
.pagination .page-options .select-dropdown .select-dropdown-list {
  border: 1px solid #eee;
  list-style: none;
  background-color: white;
  padding: 0;
}
.pagination .page-options .select-dropdown .select-dropdown-list .select-item {
  list-style: none;
  padding: 0 8px;
}
.pagination .page-options .select-dropdown .select-dropdown-list .select-item:hover {
  background-color: #ddd;
}
.pagination .page-options .select-dropdown .select-dropdown-list .select-item.select-item-selected {
  background-color: #2d8cf0;
  color: white;
}
.pagination .select-single {
  line-height: 24px;
  height: 24px;
  border: 1px solid #eee;
  padding: 1px 7px 0px 7px;
  cursor: pointer;
}
.pagination .select-single .ivu-select-arrow {
  position: initial;
}
.pagination .select-single .select-selection {
  margin-top: -2px;
}
.pagination .select-single:hover {
  background-color: #f5f5f5;
}
.pagination .page-input input {
  height: 16px;
  width: 30px;
  line-height: 16px;
  border: 1px solid #dddee1;
  vertical-align: middle;
  margin-top: -4px;
  font-size: 12px;
}



