
.selectedRow {
  background-color: whitesmoke;
  margin-bottom: 15px;
  padding: 5px;
}
.selectedTag:hover {
  border-color: #da2626 !important;
}
.selectedTag:hover i.ivu-icon-ios-close-empty:before {
  color: #ffffff;
  opacity: 1;
}
.selectedTag:hover i.ivu-icon-ios-close-empty {
  z-index: 1;
}
.selectedText {
  font-size: 14px;
  font-weight: bold;
  color: #464c5b;
}
.selectedTag:hover:after {
  background-color: #da2626 !important;
  width: 22px;
  right: 0;
}
.u-query {
  padding: 0px;
}
.u-query .line {
  height: 1px;
}
.u-query:before {
  content: "";
  display: block;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.u-query .collapse-line {
  text-align: center;
  border-top: 1px solid #eee;
  height: 24px;
  margin-bottom: 10px;
}
.u-query .collapse-line .showMoreBtn {
  position: absolute;
  border-top: 1px solid white;
  border-left: 1px solid #eee;
  border-right: 1px solid #eee;
  border-bottom: 1px solid #eee;
  padding: 1px 10px;
  -moz-border-radius-bottomleft: 5px;
  -moz-border-radius-bottomright: 5px;
  cursor: pointer;
  margin: 0 auto;
  margin-top: -1px;
  font-size: 12px;
  color: #0000008f;
  left: 50%;
}
.u-query .collapse-line:hover {
  border-top: 1px solid red;
}
.u-query .collapse-line:hover .showMoreBtn {
  color: #ff5d4b;
  border-color: #ff5d4b;
  border-top: 1px solid white;
}



